html {
  box-sizing: border-box; }

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.main-block {
  display: flex;
  justify-content: center;
  color: #ffffff;
  background-color: #691a4f; }

.main-block__inner {
  width: 1200px;
  padding: 30px 20px 51px; }

.main-block__top {
  display: flex;
  justify-content: space-between; }

.main-block__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: -5px; }

.main-block__logo a {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: bold;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.02em; }

.main-block__phone a {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: #ffffff; }

.main-block__container {
  width: 396px; }

.main-block__inner h1 {
  margin: 71px 0 0 0;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: bold;
  font-size: 109px;
  line-height: 108px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  text-align: right; }

.main-block__inner h1 span {
  font-weight: normal;
  font-size: 49px;
  line-height: 59px;
  letter-spacing: 0.03em; }

.main-block__promo {
  list-style: none;
  width: 132px;
  margin: -89px 0 0 0;
  padding: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 150%;
  text-align: right; }

.main-block__button {
  display: flex;
  justify-content: flex-end;
  margin: 83px -1px 0 0; }

.main-block__button a {
  display: block;
  width: 234px;
  padding: 20px 20px 19px;
  min-height: 61px;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  border: 4px solid #ffffff; }
  .main-block__button a:hover {
    opacity: 0.6; }
  .main-block__button a:active {
    opacity: 0.3; }

.main-block__photo {
  width: 500px;
  height: 556px;
  margin: 0 122px 0 0;
  background: url("../img/superman-desktop@1x.jpg") no-repeat 0 0;
  background-size: 500px 556px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .main-block__photo {
      background-image: url("../img/superman-desktop@2x.jpg"); } }

.main-block__social {
  list-style: none;
  margin: auto 0 41px;
  padding: 0;
  text-align: right; }

.main-block__social li {
  margin: 15px 0 0; }

.main-block__social a {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff; }
  .main-block__social a:hover {
    opacity: 0.6; }
  .main-block__social a:active {
    opacity: 0.3; }

@media (max-width: 1199px) {
  .main-block__inner {
    padding: 30px 15px 51px; } }
